$backgroundColor: #ffffff ;
$bodyColor: #000000 ;
$bodyFont: Simonetta, Calibri, Arial, sans-serif ;
$bodyFontSize: 15pt ;

* {
    -webkit-box-sizing:border-box;
    -moz-box-sizing:border-box;
    box-sizing:border-box;
    padding:0;
    margin:0
}

.titolo-1 {
    font-size: 1.8rem;
    font-weight: bold;
}
.titolo-2 {
    font-size: 1.2rem;
    font-weight: bold;
}
.grayscale {
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}
.bianconero {
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
}

.box {
    font-size: 0;
    position: relative;
    display: inline-block;
}

.fa {
    margin-left: 0.25rem;
    color: #888
}

.left {
    float: left !important;
}

.right {
    float: right !important;
}

/**
 *
 *  Stili di base.
 *
 */

H1, H2, H3, H4 {
    margin: auto;
}
H1 {
    font-size: 2rem;
}
H2 {
    margin-top: 1rem;
    font-variant-caps: petite-caps;
    font-size: 1.6rem
}
H3 {
    font-size: 1.4rem;
    margin-top: 1rem;
}
H4 {
    font-size: 1.2rem;
    margin-top: 1rem;
}
HR {
    border: none;
    text-align: center;
    margin: 0.5rem auto;
}
HR:before {
 content: 'æ°£';
}
P {
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    hyphens: auto;
}
P.newpage {
  page-break-after: always;
}
CODE, PRE {
    text-align: left;
    background-color: #e4e6c1;
    font-size: 10pt;
    font-family: Monaco;
}
I {
    color: #555;
    font-size: inherit;
}
A {
    color: #333;
}
A.nota {
    font-size: 0.8rem;
    color: #990;
    text-decoration: underline;
    vertical-align: super;
    display: inline-block;
    margin-left: 0.12rem; 
}
SUP {
    font-size: 0.7em;
    vertical-align: super;
    line-height: 0;
}
DIV {
 display: inline-block;
}
UL {
    list-style-type: square;
}
OL {
    padding: 0 2rem;
    margin: 1rem 0;
}
TABLE, TD, TR, TH {
    border-collapse: collapse;
    border: solid 1px #CCC;
    padding: 0.25rem 0.5rem;
    margin: 1rem 0;
    font-size: 11pt;   
}

TABLE TH {
    font-weight: bold;
    text-align: center;
}

/** Tabella per le aree di memoria */
TABLE.memoria TD {
    font-family: Monaco;
    font-size: 10pt;
    width: 20px;
    height: 20px;
}
TABLE.memoria TD.area {
    background: #aaa;
}


/**
 *
 *  Stili per il corpo della pagina.
 *
 */
BODY {
    background-color: $backgroundColor;
    color: $bodyColor;
    font-family: $bodyFont;
    font-size: $bodyFontSize;
    text-align: center;
}

DIV#page {
    padding: 1rem 1rem 5rem 1rem;
    position: relative;
    display: block;
    max-width: 34rem;
    margin: auto;
}

HEADER {
}

HEADER DIV.box.intestazione {
    width: 100%;
    text-align: left;
}

HEADER DIV.box.intestazione {
    width: 100%;
    padding-bottom: 1rem;
    text-align: left;
}

HEADER img.logo {
    height: 80px;
    width: 130px;
    display: inline-block;
    float: left;
    padding: 0 1rem;
}

FOOTER {
    padding: 3rem 0 0 0;
}


/**
 *
 *  Stili dei menu
 *
 */
NAV {
     border-top: solid 1px;
     display: inline-block;
     border-bottom: solid 1px;
     border-color: #990;
     width: 100%;
     font-size: 0;
}
NAV A {
    font-variant-caps: petite-caps;
    font-size: 13pt;
    font-weight: bold;
    display: inline-block;
}

HEADER NAV.menu-top A {
    padding: 0.5rem 1.2rem;
}

/**
 *
 *  Stili per il corpo della pagina.
 *
 */
MAIN {
    width: 100%;
    display: inline-block;
    margin: auto;
    text-align: justify;
    position: relative;
    padding-top: 1rem
}

MAIN P+P {
    margin-top: 1rem;
}

MAIN.bozza {
    background-color: #c1e3e6;;
    padding: 5rem 1rem;
}

MAIN.bozza::before {
    content: "BOZZA";
    font-size: 1rem;
    font-weight: bold;
    position: absolute;
    top: 2rem;
    left: 2rem;
    font-family: Monaco;
}

MAIN P.immagine {
    margin: 1rem auto;
    text-align: center
}

MAIN P.immagine.chi {
    margin: 0.5rem auto;
}

MAIN P.immagine IMG {
    border: solid 1px #999;
    text-align: center;
    max-width: 100%;
}

MAIN BLOCKQUOTE {
    color: #444;
    text-align: left;
    font-size: 14pt;
    padding: 1em;
    font-style: italic
}

/**
 *  Stili per le pagine normali
 */
BODY.pagina HEADER DIV.box.titoli {
    padding: 0.5rem 0;
    width: calc(100% - 130px);
}

BODY.pagina HEADER titolo-2 {
    margin-top: 0;
    padding-bottom: 0;
    font-size: 1.4rem
}

BODY.pagina H1 {
    margin: 3rem 0 6rem 0;
    text-align: center;
}

BODY.faq SECTION {
    padding: 1rem;
}

/**
 *
 *  Stili per la pagina index 
 *
 */
BODY.index HEADER DIV.box.intestazione {
    text-align: center;
    padding: 0;
}
 
BODY.index HEADER DIV.box.titoli {
    padding: 1rem 0;
}

BODY.index HEADER titolo-1, 
BODY.manuale.copertina HEADER titolo-1 {
    padding: 1rem 0;
    text-align: center;
    display: block;
}

BODY.index HEADER titolo-2 {
    margin-bottom: 0.5rem;
    line-height: unset;
    float: none;
}

BODY.index HEADER img.logo {
    width: 240px;
    height: auto;
    max-width: 90%;
    float: none;
}

/**
 *
 *  Stili per il manuale
 *
 */
BODY.manuale DIV#page {
    min-height: 90vh;
}

BODY.manuale HEADER titolo-2 {
    float: unset
}

BODY.manuale HEADER DIV.box.titoli {
    width: 100%;    
}

BODY.manuale HEADER DIV.box.titoli SPAN {
    font-size: 10pt;
    color: #999;
}

BODY.manuale HEADER NAV.menu-top {
    border: none;
}

BODY.manuale FOOTER {
}

BODY.manuale.copertina MAIN {
    text-align: center;
}

BODY.manuale.copertina MAIN IMG {
    padding: 3rem 0;
}

BODY.manuale.copertina MAIN H2 {
    padding: 0;
}

BODY.manuale.dedica MAIN {
    margin: 15vh auto 15vh auto;
}

BODY.manuale.dedica MAIN P {
    text-align: center;
    max-width: 20rem;
    margin: auto
}

BODY.manuale.indice H3.bozza A {
    color: #999
}

BODY.manuale.indice H3 {
    margin: 1rem 0
}

BODY.manuale.indice H3.home {
    margin-top: 4rem;
}

BODY.manuale.indice MAIN {
 width: auto;
 text-align: center
}

BODY.manuale.indice IMG.logo {
    width: 100px;
    margin: auto
}

BODY.manuale.indice SECTION.indice {
 text-align: center;
}

BODY.manuale H1 {
    margin: 3rem 0 3rem 0;
    text-align: center;
}

BODY.manuale MAIN PRE {
    display: block;
    padding: 1rem;
    margin: 1rem 0;
    white-space: pre-wrap;
}

BODY.manuale MAIN P A {
 color: #990;
}

BODY.manuale MAIN UL {
 margin: 1rem 0;
 padding-left: 2.5rem;
}

BODY.manuale BLOCKQUOTE.incluso {
 color: #543;
 text-align: justify;
 padding: 1rem;
 background: #e4e6c1;
 margin: 1rem 0;
}

BODY.manuale BLOCKQUOTE.motto {
    text-align: right;
    padding: 2rem 0 4rem 0;
    font-size: 0.9em
}

BODY.manuale IMG.logo {
 width: 300px;
 max-width: 90%;
 display: inline-block;
}

IFRAME.pdf {
    width: 100%;
    height: 780px;
}

/**
 *  Stili per mobile
 */
@media only screen and (max-width: 420px) {

    BODY {
    }
    H1 {
        font-size: 1.75rem;
    }
    H2 {
        font-size: 1.2rem
    }
    UL, OL {
        padding: 0 1rem 0 1.5rem;
    }
    BODY.pagina HEADER titolo-1 {
        font-size: 1.5rem;
    }
    BODY.pagina HEADER titolo-2 {
        font-size: 1rem;
    }
    BODY.index HEADER DIV.box.titoli, 
    BODY.manuale.copertina HEADER DIV.box.titoli {
        width: auto;    
    }
    BODY.index HEADER titolo-1, 
    BODY.manuale.copertina HEADER titolo-1 {
        padding: 0;
    }
    BODY.index HEADER img.logo, 
    BODY.manuale.copertina HEADER img.logo {
        width: 200px;
        height: auto;
    }
    BODY.pagina MAIN OL {
        text-align: left;
    }

    NAV.menu-top DIV.box {
        display: block;
    }

    NAV.menu-top A {
        font-size: 14pt;
        padding: 0;
    }

    DIV.testo P {
        text-align: left
    }

}
